<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">新增退货单</h1>
          
          <label for="">退货数量</label><el-input v-model="task.rejection_goods_count" placeholder="退货数量"></el-input>
          <label for="">对应采购单</label><el-input v-model="task.purchases_detail_id" placeholder="对应采购单"></el-input>
          <label for="">仓库</label><el-input v-model="task.warehouse_id" placeholder="仓库"></el-input>
          
          <!-- <label for="">商品ID</label><el-input v-model="task.goods_id" placeholder="商品ID"></el-input>
          <label for="">价格</label><el-input v-model="task.rejection_goods_price" placeholder="价格"></el-input>
          <label for="">退货总额</label><el-input v-model="task.rejection_money" placeholder="退货总额"></el-input>
          <label for="">状态</label><el-input v-model="task.status" placeholder="状态"></el-input>
          <label for="">供应商</label><el-input v-model="task.supplier_id" placeholder="供应商"></el-input>
          <label for="">仓库</label><el-input v-model="task.warehouse_id" placeholder="仓库"></el-input>
          <label for="">对应收货单</label><el-input v-model="task.pw_bill_detail_id" placeholder="对应收货单"></el-input>  -->
           
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              rejection_goods_count: "",
              purchases_detail_id: "" ,
              warehouse_id: "" ,

              //goods_id: "",
              //rejection_goods_price: "",
              //rejection_money: "",
              //status: "" ,
              //supplier_id: "" ,
              //pw_bill_detail_id: "" , 
            },
           
            warehouseOptions: [
              {
                label: '国内仓',
                value: '1'
              },
              {
                label: '泰国仓',
                value: '2'
              } 
            ],
          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/pr-bill";
          let params = {}; 
          params.rejection_goods_count = this.task.rejection_goods_count; 
          params.purchases_detail_id = this.task.purchases_detail_id;
          params.warehouse_id = this.task.warehouse_id;
           
          let that = this;  
          this.$axios.post(url,params).then((res) => {
             
              that.$message({
                type: 'success',
                message: '新增成功!'
              }); 
            
          }).catch((err) => {
             
              that.$message({
                type: 'error',
                message: '新增失败!'
              }); 
          })
        },
         
      },
      created() {
         
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



